import React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import Image2 from './image/about.jpeg';
import Image1 from './image/chefab.png';
import './style/about.css';

export const Services = () => {
  return (
    <div fluid="md" className='mainAbout'>
        <div className="about">
      <h1>SERVICES</h1>
  </div>
<Row>
  <Col className="para">
    <p>
        Food destiny is one of the countrys most celebrated restaurants; the creation of leading Australian restaurant group,
         Fink, and Executive Chef Peter Gilmore.
    </p>
<p>
The restaurant is an organic space reflective of Peter Gilmores nature inspired cuisine. The interplay of textures and colour brings 
life and a vibrance that embraces the restaurants place in the dress circle of Sydney Harbour. An ode to the Australian landscape,
 from the vast ocean floor,to the cracked bark of a paperbark tree, every detail from the ground up has been thoughtfully considered.
    </p>
    </Col>


<Col className="image1">
    <img width="500px" height="400px" src={Image2} alt="fooddestiny"/>
</Col>
</Row>
<Row>
    <Col className="image2">
    <img width="500px" height="400px" src={Image1} alt="fooddestiny"/>
</Col>
    <Col className="para2"><p>
    Our core values prioritise being customer-focused to provide excellent products and service and employee-focused by maintaining a superior
    quality work environment. Our Company was ranked 13th among best companies to work for in India across all categories by Economic Times 
     Great Places to Work survey for 2019 conducted by Great Places to Work Institute. Our Company was also voted among the top ten employers 
 within the Indian retail industry for 2019 by Great Places to Work and 14th among best large workplaces in Asia by Great Places to Work Institute.
    </p></Col>
    
    </Row>
    </div>  
  )
}
