import React, { useState, useEffect } from 'react';
import { Col, Container, Card, Row, Spinner} from 'react-bootstrap';
import Usereducer from './Reducer/UseReducer';
const axios = require('axios');

export default function MainCourse() {
  const [users, setUsers] = useState(false)

const getRepo = ()=> {
  axios.get('https://pixabay.com/api/?key=23049742-013206ee8de368b5183f366f3&q=food+indian&image_type=food&pretty=true&per_page=default:20')
.then((response)=>{
    console.log(response)
var myRepo = response.data.hits;    
   setUsers(myRepo);
   console.log(users)
});
};
useEffect(() => {
  const timer = setTimeout(() => {getRepo()}, 2000);
  return () => clearTimeout(timer);
}, []);

    return <> 
    {users ? <div style={{marginLeft:"100px"}}>
         <h1 className='text-center '>Main Course</h1>
         <div>
         <Container>
         <Row md={3}>
             {users.map((res) => {
                 return (
                     <Col style={{marginBottom:"100px"}}>
                     <Card style={{width:"400px",margin:"10px",textAlign:"center"}} >
                     <Card.Body>
                     <Card.Title> {res.tags}  </Card.Title>
                    <Card.Img width="100px" height="300px" variant="top" src={res.largeImageURL} />
                      <Card.Footer  className='text-center'> Price= {res.comments}Rs</Card.Footer>
                      <Usereducer/>
                     </Card.Body>
                   </Card>
                   </Col>
                   
                     
                 )
             })
             } 
             </Row>
             </Container>
             </div>
     </div> :<div style={{height:"500px",width:"900px",margin:"10% 50%"}}> <Spinner style={{height:"150px",width:"150px"}} animation="border" variant="warning" role="status">
   <span className="visually-hidden">Loading..</span>
 </Spinner></div>}
     </>
}
