import React,{useReducer} from 'react'
import { Button,ButtonGroup } from 'react-bootstrap'

const Usereducer = () => {
    const intialstate=0;

    const reducer=(state,action)=>{
        console.log(state,action);
        if(action.type==='inc')
            state=state+1;
    
        if(action.type==='dec')   
            state=state-1;

        if(action.type==='reset')
             state=0
        return state;
        
    }

    const [state,disptach]=useReducer(reducer,intialstate)
  return (
    <div>
        <p>{state ? state : ""}</p>
        {state==0 ? 
<ButtonGroup aria-label="Basic example">
  <Button onClick={()=>disptach({type:'inc'})} variant="secondary">Add to Cart</Button>
</ButtonGroup> :
<ButtonGroup aria-label="Basic example">
  <Button onClick={()=>disptach({type:'inc'})} variant="secondary">Add to Cart</Button>
  <Button onClick={()=>disptach({type:'dec'})} variant="secondary">Remove</Button>
</ButtonGroup> }

    </div>
  )
}

export default Usereducer