import React, { Component } from 'react';
import { Button } from 'react-bootstrap';
import ReactFormInputValidation from "react-form-input-validation";

export  class About extends Component {
    constructor(props) {
        super(props);
        this.state = {
          fields: {
            username: "",
            message: "",
            contact: "",
            email:"",
          },
          errors: {}
        };
        
        this.form = new ReactFormInputValidation(this);
        this.form.useRules({
            username: "required",
            email: "required|email",
            contact: "required|numeric|digits_between:10,12",
            message:"required" ,
            
        });

        this.form.onformsubmit = (fields) => {
          // Do you ajax calls here.
        }
    }
    
  render() {
    return <div className='formdiv'>
        <h1 style={{textAlign:"center"}}>Contact Page</h1>
      <form className='Form' onSubmit={this.form.handleSubmit}  >
            <p>
              <label>
                UserName
                <input
                  type="text"
                  name="username"
                  onBlur={this.form.handleBlurEvent}
                  onChange={this.form.handleChangeEvent}
                  value={this.state.fields.username}
                />
              </label>
              <label className="error"> 
                {this.state.errors.username ? this.state.errors.username : ""}
              </label>
            </p>
            <p>
              <label>
                Email
                <input
                  type="email"
                  name="email"
                  onBlur={this.form.handleBlurEvent}
                  onChange={this.form.handleChangeEvent}
                  value={this.state.fields.email}
                />
              </label>
              <label className="error"> 
                {this.state.errors.email ? this.state.errors.email : ""}
              </label>
            </p>
 
            <p>
              <label>
                Contact No
                <input
                  type="tel"
                  name="contact"
                  onBlur={this.form.handleBlurEvent}
                  onChange={this.form.handleChangeEvent}
                  value={this.state.fields.contact}
                />
              </label>
              <label className="error"> 
                {this.state.errors.contact ? this.state.errors.contact : ""}
              </label>
            </p>
            <p>
              <label>
                message
                <textarea
                  type="text"
                  name="message"
                  onBlur={this.form.handleBlurEvent}
                  onChange={this.form.handleChangeEvent}
                  value={this.state.fields.message}
                />
              </label>
              <label className="error"> 
                {this.state.errors.message ? this.state.errors.message : ""}
              </label>
            </p>
            <p>
              <Button  type="submit" >Submit</Button>
            </p>
        </form>

        <div>
          <p>{this.state.fields.username}</p>
        </div>
    </div>;
  }
}
